import * as actionTypes from './actions';

export const randomNumberGenerate = (digitCounter, changerFn) => {
    return{
        type: actionTypes.RANDOM_NUMBER,
        digitCount: digitCounter,
        rowType: changerFn
    }
}
export const randomNumberGenerateOnStart = (topRow, bottomRow) => {
    return{
        type: actionTypes.GENERATEONSTART,
        topRow: topRow,
        bottomRow: bottomRow
    }
}
export const generatedOnStart = (topRow, bottomRow) => {
    return{
        type: actionTypes.GENERATEDONSTART,
        topRow: topRow,
        bottomRow: bottomRow
    }
}
export const inputNumber = (updatedNumbers, changerFn) => {
    return{
        type: actionTypes.INPUT_NUMBER,
        newNumbers: updatedNumbers,
        rowPosition: changerFn
    }
}
export const startMultiplication = (topRow, bottomRow) => {
    return{
        type: actionTypes.START,
        topRow: topRow,
        bottomRow: bottomRow
    }
}
export const endMultiplication = () => {
    return{
        type: actionTypes.END
    }
}
export const stepCounter = (position, upperDigit, lowerDigit, resultDigits) => {
    return{
        type: actionTypes.COUNTER,
        position: position,
        upperDigit: upperDigit,
        lowerDigit: lowerDigit,
        resultDigits: resultDigits
    }
}
export const prevCounter = (position, upperDigit, lowerDigit, resultDigits) => {
    return{
        type: actionTypes.COUNTERPREV,
        upperDigit: upperDigit,
        lowerDigit: lowerDigit,
        position: position,
        resultDigits: resultDigits
    }
}
export const carryAddition = (position, carryNumbers, upperDigit, lowerDigit) => {
    return{
        type: actionTypes.COUNTERCARRY,
        position: position,
        carryNumbers: carryNumbers,
        upperDigit: upperDigit,
        lowerDigit: lowerDigit
    }
}
export const resultAddition = (position, upperDigit, lowerDigit) => {
    return{
        type: actionTypes.COUNTERRESULT,
        position: position,
        upperDigit: upperDigit,
        lowerDigit: lowerDigit
    }
}

export const inputFocus = (current, changeFn) => {
    return{
        type: actionTypes.INPUTFOCUS,
        current: current,
        position: changeFn
    }
}

export const keypress = (key, index, changeFn) => {
    return{
        type: actionTypes.KEYPRESS,
        key: key,
        index: index,
        position: changeFn
    }
}
export const multiply = () => {
    return{
        type: actionTypes.KEYPRESSMULTI
    }
}
export const deleteDigit = (index, position) => {
    return{
        type: actionTypes.DELETEDIGIT,
        index: index,
        position: position
    }
}