import * as actionTypes from '../actions/actions';

const initialState = {
    inputFocusTop:{
        0: true,
        1: false,
        2: false
    },
    inputFocusBottom:{
        0: false,
        1: false,
        2: false
    }
};
const falsyObject = {
    inputFocusTop:{
        0: false,
        1: false,
        2: false
    },
    inputFocusBottom:{
        0: false,
        1: false,
        2: false
    }
}
const numberPad = (state = initialState, action)=>{
    if(action.type === actionTypes.START || action.type === actionTypes.GENERATEDONSTART){
        return {
            ...falsyObject
        }
    }
    switch (action.type) {
        case actionTypes.INPUTFOCUS:
            return {
                ...falsyObject,
                [action.position]:{
                    ...falsyObject[action.position],
                    [action.current] : true
                }
            }
        case actionTypes.KEYPRESSMULTI:
            return {
                ...falsyObject,
                inputFocusBottom:{
                    ...falsyObject.inputFocusBottom,
                    0: true
                }
            }
        default:
            return state
    };
};

export default numberPad;